import React from 'react';
import logo from './logo.svg';
import './App.css';
import { BrowserRouter as Router, Link, Switch, Route, useParams, useRouteMatch,useLocation } from 'react-router-dom';

// BrowserRouter a.k.a Router communicates/interacts with Browser History APIs to perform navigation.


function AboutUs() {
  return (
    <div>Hello from the about us page</div>
  )
}

function ContactUs() {
  return (
    <div>Hello from the Contact us page</div>
  )
}

function HomePage() {
  return (
    <div>Hello from the home page</div>
  )
}

function UserInformation() {

  const { userid, param2 } = useParams();

  return (
    <div>{userid === undefined ? 'You oughtta pass user id' : 'Displaying user info with ID ' + userid} </div>
  )
}

function ComponentVsProps() {
  return (
    <div>ComponentVsProps</div>
  )
}

function State() {
  return (
    <div>State</div>
  )
}

function Props() {
  return (
    <div>Props</div>
  )
}

function useQuery(){
  //?pagenumber=10&sorting=firstname
   return new URLSearchParams(useLocation().search)
}

function Topic() {
  debugger

  const QueryParams = useQuery();

  // const { path, url } = useRouteMatch();
  // const { topicID } = useParams();
  return (
    <div>
      {/* <ul>
        <h1>You are inside specific topic</h1>
        <li><Link to={`${url}/diagram1`}>Components vs props diagram</Link></li>
        <li><Link to={`${url}/diagram2`}>State diagram</Link></li>
      </ul>

      <Switch>
        <Route path={`${path}/diagram1`}>You are seeing Components vs props diagram</Route>
        <Route path={`${path}/diagram2`}>You are seeing State diagram</Route>
      </Switch> */}
      <h1>You are visiting this page with pagenumber : {QueryParams.get('pagenumber')} </h1>
      <h1>You are visiting this page with sorting : {QueryParams.get('sorting')} </h1>
    </div>
  )
}

function Topics() {
  debugger
  const { path, url } = useRouteMatch();
  return (
    <div>
      <h1>Topics</h1>
      <ul>
        <li><Link to={`${url}/component-vs-props`}>Components vs props</Link></li>
        <li><Link to={`${url}/state`}>State</Link></li>
        <li><Link to={`${url}/props`}>Props</Link></li>
        <li><Link to={`${url}/virtual-dom`}>Virtual Dom</Link></li>
      </ul>

      <Switch>
        {/* <Route path={`${path}/component-vs-props`}><ComponentVsProps/></Route>
        <Route path={`${path}/state`}><State/></Route>
        <Route path={`${path}/props`}><Props/></Route> */}
        <Route path={`${path}/:topicID`}><Topic /></Route>
        <Route path={`${path}`}>Please choose the topic</Route>
      </Switch>


    </div>
  )
}

function App() {
  return (
    <div>
      <Router>
        <nav>
          <ul>
            <li><Link to={process.env.PUBLIC_URL + '/'}>Home</Link></li>
            <li><Link to={process.env.PUBLIC_URL + '/contact-us'}>Contact Us</Link></li>
            {/* <li><Link to='/about-us'>About Us</Link></li>
            <li><Link to='/user'>User</Link></li>
            <li><Link to='/topics'>Topics</Link></li> */}
          </ul>
        </nav>

        <Switch>
          <Route exact path={process.env.PUBLIC_URL + '/'}><HomePage /></Route>
          <Route path={process.env.PUBLIC_URL + '/contact-us'}><ContactUs /></Route>
          {/* <Route path='/about-us' component={AboutUs}></Route>
          <Route path='/user/:userid?/:param2?'><UserInformation /></Route>
          <Route path='/topics'><Topics /></Route>
          <Route path='*'>Not found</Route> */}
        </Switch>

      </Router>
    </div>
  );
}

export default App;
